<template>
  <div class="container">
    <!--  -->
    <van-nav-bar fixed left-arrow @click-left="$router.back()" title="" class="page-nav-bar"></van-nav-bar>

    <!--  -->
    <div class="chat-list">
      <div v-for="(obj, index) in list" :key="index">
        <!--  -->
        <div class="chat-item left" v-if="obj.name !== 'me'">
          <van-image fit="cover" round src="https://img.yzcdn.cn/vant/cat.jpeg" />
          <div class="chat-pao">{{ obj.msg }}</div>
        </div>

        <!--  -->
        <div class="chat-item right" v-else>
          <div class="chat-pao">{{ obj.msg }}</div>
          <van-image fit="cover" round :src="$store.state.avatar" />
        </div>
      </div>
    </div>

    <!--  -->
    <div class="reply-container van-hairline--top">
      <van-field v-model="word" placeholder="...">
        <template #button>
          <span style="font-size: 12px; color: #999" @click="sendFn"></span>
        </template>
      </van-field>
    </div>
  </div>
</template>

<script>
// 1. , io
import { io } from 'socket.io-client'
export default {
  name: 'ChatIndex',
  data() {
    return {
      word: '', // 
      list: [
        // 
        //  name 
        { name: 'xs', msg: 'hi' },
        { name: 'me', msg: '' },
        { name: 'xs', msg: 'hi' },
        { name: 'me', msg: '' },
        { name: 'xs', msg: 'hi' },
        { name: 'me', msg: '' },
        { name: 'xs', msg: 'hi' },
        { name: 'me', msg: '' }
      ],
      socket: null // socket
    }
  },
  created() {
    // axios
    this.socket = io('ws://geek.itheima.net', {
      query: {
        token: this.$store.state.user.token
      },
      transports: ['websocket']
    })
    // ()
    this.socket.on('connect', () => {
      console.log('')
    })
    // 
    this.socket.on('message', (obj) => {
      this.list.push({
        name: 'xs',
        msg: obj.msg
      })
      // dom
      // const endDiv = document.querySelector('.chat-list>div:last-child')
      // console.log(endDiv)
    })
  },
  methods: {
    sendFn() {
      if (this.word.trim().length === 0) return
      // 
      this.socket.emit('message', {
        msg: this.word,
        timestemp: new Date().getTime()
      })
      // 
      this.list.push({
        msg: this.word,
        name: 'me'
      })
      // 
      this.word = ''
    }
  },
  beforeDestroy() {
    this.socket.close()
    this.socket = null
  },
  watch: {
    list: {
      deep: true,
      handler(val) {
        // console.log(endDiv)
        // domdomdomnextTict
        this.$nextTick(() => {
          const endDiv = document.querySelector('.chat-list>div:last-child')
          endDiv.scrollIntoView({
            behavior: 'smooth'
          })
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.container {
  height: 100%;
  width: 100%;
  position: absolute;
  left: 0;
  top: 0;
  box-sizing: border-box;
  background: #fafafa;
  padding: 80px 0 100px 0;
  .chat-list {
    height: 88vh;
    overflow-y: scroll;
    .chat-item {
      padding: 20px;
      .van-image {
        vertical-align: top;
        width: 80px;
        height: 80px;
      }
      .chat-pao {
        vertical-align: top;
        display: inline-block;
        min-width: 80px;
        max-width: 140%;
        min-height: 80px;
        line-height: 76px;
        border: 1px solid #c2d9ea;
        border-radius: 8px;
        position: relative;
        padding: 0 20px;
        background-color: #e0effb;
        word-break: break-all;
        font-size: 27px;
        color: #333;
        &::before {
          content: '';
          width: 20px;
          height: 20px;
          position: absolute;
          top: 24px;
          border-top: 1px solid #c2d9ea;
          border-right: 1px solid #c2d9ea;
          background: #e0effb;
        }
      }
    }
  }
}
.chat-item.right {
  text-align: right;
  .chat-pao {
    margin-left: 0;
    margin-right: 30px;
    &::before {
      right: -12px;
      transform: rotate(45deg);
    }
  }
}
.chat-item.left {
  text-align: left;
  .chat-pao {
    margin-left: 30px;
    margin-right: 0;
    &::before {
      left: -10px;
      transform: rotate(-135deg);
    }
  }
}
.reply-container {
  position: fixed;
  left: 0;
  bottom: 0;
  height: 88px;
  width: 100%;
  background: #f5f5f5;
  z-index: 9999;
}
</style>
